import React from 'react';
import { Global, css } from '@emotion/react';
import Common from './common';

// 기본 스타일
const baseStyle = css`
 /* Colors */
 :root {
  --color-primary: ${Common.lightColor.primary};
  --color-text: ${Common.lightColor.text};
  --color-textLight: ${Common.lightColor.textLight};
  --color-heading: ${Common.lightColor.heading};
  --color-headingBlack: ${Common.lightColor.headingBlack};
  --color-accent: ${Common.lightColor.accent};
  --color-tag: ${Common.lightColor.tag};
  --color-link: ${Common.lightColor.link};
  --color-background: ${Common.lightColor.background};
  --color-unselectedtext: ${Common.lightColor.unselectedtext};
  --color-gradiant: ${Common.lightColor.gardiant};
 }

 @import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

 @font-face {
  font-family: 'IBMPlexSansKR-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/IBMPlexSansKR-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
 }

 /* HTML elements */
 *,
 :after,
 :before {
  box-sizing: border-box;
 }

 html {
  line-height: 1.75;
  font-size: ${Common.fontSize.root};
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0.02rem;
  word-break: keep-all;
 }

 body {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: ${Common.fontSize[1]};
  color: var(--color-text);
  background-color: var(--color-background);
 }

 hr {
  background: var(--color-accent);
  height: 1px;
  border: 0;
 }

 /* Heading */
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
  margin-bottom: ${Common.spacing[3]};
 }

 h2,
 h3,
 h4,
 h5,
 h6 {
  font-weight: ${Common.fontWeight.bold};
  color: var(--color-heading);
 }

 h1 {
  font-weight: ${Common.fontWeight.black};
  font-size: ${Common.fontSize[5]};
  color: var(--color-headingBlack);
 }

 h2 {
  font-size: ${Common.fontSize[4]};
 }

 h3 {
  font-size: ${Common.fontSize[3]};
 }

 h4 {
  font-size: ${Common.fontSize[2]};
 }

 h5 {
  font-size: ${Common.fontSize[1]};
 }

 h6 {
  font-size: ${Common.fontSize[0]};
 }

 h1 > a {
  color: inherit;
  text-decoration: none;
 }

 h2 > a,
 h3 > a,
 h4 > a,
 h5 > a,
 h6 > a {
  text-decoration: none;
  color: inherit;
 }

 /* Prose */
 p {
  --baseline-multiplier: 0.179;
  --x-height-multiplier: 0.35;
  margin: ${Common.spacing[0]} ${Common.spacing[0]} ${Common.spacing[7]} ${Common.spacing[0]};
  padding: ${Common.spacing[0]};
 }

 ul,
 ol {
  margin-left: ${Common.spacing[0]};
  margin-right: ${Common.spacing[0]};
  margin-bottom: ${Common.spacing[8]};
  list-style-position: outside;
  list-style-image: none;
 }

 ul li,
 ol li {
  margin-bottom: calc(${Common.spacing[4]} / 2);
 }

 li > p {
  margin-bottom: calc(${Common.spacing[4]} / 2);
 }

 li *:last-child {
  margin-bottom: ${Common.spacing[0]};
 }

 li > ul {
  margin-left: ${Common.spacing[8]};
  margin-top: calc(${Common.spacing[8]} / 2);
 }

 /* Link */
 a {
  color: var(--color-link);
  text-decoration: none;
 }

 a:hover,
 a:focus {
  text-decoration: none;
 }
 /*
 * Based on Plugin: Syntax Highlighter CB
 * Plugin URI: http://wp.tutsplus.com/tutorials/plugins/adding-a-syntax-highlighter-shortcode-using-prism-js
 * Description: Highlight your code snippets with an easy to use shortcode based on Lea Verou's Prism.js.
 * Version: 1.0.0
 * Author: c.bavota
 * Author URI: http://bavotasan.comhttp://wp.tutsplus.com/tutorials/plugins/adding-a-syntax-highlighter-shortcode-using-prism-js/ */
 /* http://cbavota.bitbucket.org/syntax-highlighter/  */

 /* =====   ===== */
 code[class*='language-'],
 pre[class*='language-'] {
  color: #fff;
  text-shadow: 0 1px 1px #000;
  font-family: Menlo, Monaco, 'Courier New', monospace;
  direction: ltr;
  text-align: left;
  word-spacing: normal;
  white-space: pre;
  word-wrap: normal;
  line-height: 1.4;
  background: none;
  border: 0;
  font-size: 14px;

  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
 }

 pre[class*='language-'] code {
  float: left;
  padding: 0 15px 0 0;
 }

 pre[class*='language-'],
 :not(pre) > code[class*='language-'] {
  background: #222;
 }

 /* Code blocks */
 pre[class*='language-'] {
  padding: 15px;
  margin: 1em 0;
  overflow: auto;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
  border-radius: 8px;
 }

 /* Inline code */
 :not(pre) > code[class*='language-'] {
  padding: 5px 10px;
  line-height: 1;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
 }

 .token.comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
  color: #797979;
 }

 .token.selector,
 .token.operator,
 .token.punctuation {
  color: #fff;
 }

 .token.namespace {
  opacity: 0.7;
 }

 .token.tag,
 .token.boolean {
  color: #ffd893;
 }

 .token.atrule,
 .token.attr-value,
 .token.hex,
 .token.string {
  color: #b0c975;
 }

 .token.property,
 .token.entity,
 .token.url,
 .token.attr-name,
 .token.keyword {
  color: #c27628;
 }

 .token.regex {
  color: #9b71c6;
 }

 .token.entity {
  cursor: help;
 }

 .token.function,
 .token.constant {
  color: #e5a638;
 }

 .token.variable {
  color: #fdfba8;
 }

 .token.number {
  color: #8799b0;
 }

 .token.important,
 .token.deliminator {
  color: #e45734;
 }
 .style .token.string {
  color: #9a6e3a;
  background: none !important;
 }
 /* Line highlight plugin */
 .line-highlight.line-highlight {
  background: rgba(255, 255, 255, 0.2);
 }

 .line-highlight.line-highlight:before,
 .line-highlight.line-highlight[data-end]:after {
  top: 0.3em;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
  border-radius: 8px;
 }

 /* for line numbers */
 /* span instead of span:before for a two-toned border */
 .line-numbers .line-numbers-rows > span {
  /* border-right: 3px #d9d336 solid; */
 }

 .token.operator,
 .token.entity,
 .token.url,
 .language-css .token.string,
 .style .token.string {
  color: #9a6e3a;
  background: none !important;
 }
`;

// 다크 모드 스타일
const darkStyle = css`
 /* Colors */
 :root {
  --color-primary: ${Common.darkColor.primary};
  --color-textLight: ${Common.darkColor.textLight};
  --color-accent: ${Common.darkColor.accent};
  --color-link: ${Common.darkColor.link};
  --color-heading: ${Common.darkColor.heading};
  --color-headingBlack: ${Common.darkColor.headingBlack};
  --color-tag: ${Common.darkColor.tag};
  --color-text: ${Common.darkColor.text};
  --color-background: ${Common.darkColor.background};
  --color-unselectedtext: ${Common.darkColor.unselectedtext};
  --color-gradiant: ${Common.darkColor.gardiant};
 }
`;

const GlobalStyle = () => {
 // body 클래스에 따라 다른 스타일 선택
 const currentStyle = css`
  ${baseStyle};
  ${document.body.classList.contains('dark') ? darkStyle : null};
 `;

 return <Global styles={currentStyle} />;
};

export default GlobalStyle;
