import React, { useState, useCallback, useEffect } from 'react';
import { GlobalHeader, GlobalWrapper, GlobalFooter, TitleLink, DarkModeButton, FlexWrapper } from './style';
import GlobalStyle from '../../style/globalStyle';
import { Link } from 'gatsby';
import { FiSun } from 'react-icons/fi';
import { FaMoon } from 'react-icons/fa';

const Layout = ({ location, title, children }) => {
 // eslint-disable-next-line no-undef
 const rootPath = `${__PATH_PREFIX__}/`;
 const aboutPath = `${__PATH_PREFIX__}/about/`;
 const tagsPath = `${__PATH_PREFIX__}/tags/`;
 const isRootPath = location.pathname === rootPath;
 const isAboutPath = location.pathname === aboutPath;
 const isTagsPath = location.pathname === tagsPath;

 const [theme, setTheme] = useState(null);

 let isDarkMode = false;
 if (typeof window !== 'undefined') isDarkMode = theme === window.__DARK;

 const onClickDarkModeButton = useCallback(() => {
  const newTheme = isDarkMode ? window.__LIGHT : window.__DARK;
  window.__setTheme(newTheme);
  setTheme(newTheme);
 }, [isDarkMode]);

 useEffect(() => {
  setTheme(window.__theme);
 }, []);

 if (!theme) {
  return null;
 }

 return (
  <div>
   <GlobalStyle />
   <GlobalHeader>
    <FlexWrapper>
     <TitleLink to="/">✨</TitleLink>
     <Link to="/">
      <span className={isRootPath ? 'selected' : ''}>HOME</span>
     </Link>
     <Link to="/tags">
      <span className={isTagsPath ? 'selected' : ''}>TAGS</span>
     </Link>
     {/* <Link to="/about">
      <span className={isAboutPath ? 'selected' : ''}>ABOUT</span>
     </Link> */}
    </FlexWrapper>
    <FlexWrapper>
     <DarkModeButton onClick={onClickDarkModeButton}>{isDarkMode ? <FiSun /> : <FaMoon />}</DarkModeButton>
    </FlexWrapper>
   </GlobalHeader>
   <GlobalWrapper isRootPath={isRootPath}>
    <main>{children}</main>
    <br />
    <br />
    <br />
   </GlobalWrapper>
   <GlobalFooter>
    <hr />
    <br />© 2023. Built with Gatsby
   </GlobalFooter>
  </div>
 );
};

export default Layout;
